@import "./../../../styles/colors";

a {
    text-decoration: none;
    color: $dark-gray;
}
.gridItem {
    width: 100%;
    background-size: 70% 70%;
    background-repeat: no-repeat;
    background-position: center;
    background-color: white;
    &:hover {
        filter: brightness(50%);
        transition: .5s;
    }
}

.gridItemDescription {
    padding: 30px 0 0 30px;
}