@import "./../../styles/colors";
 @media (max-width: 900px) {
   .navigationItem {
     display: grid;
     margin-bottom: 10px;
   }

 }

@media (min-width: 900px) {
  .navigationItem {
    display: inline-block;
    margin: 0 30px 0 0;
  }

}
.navigationItem {

  a {
    text-decoration: none;
    color: white;
    font-size: 16px;
    &:hover {
      color: $orange;
    }
  }
}
