.dots {
  height: 15px;
  width: 15px;
  border-radius: 50%;
  border: 1px solid orange;
  background-color: #FFF;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 2px 6px #AAA
}

.output {
  margin: 30px 0;
}

.rangeContainer {
  height: 40px;
  display: flex;
  width: 100%
}

.track {
  height: 2px;
  width: 100%;
  border-radius: 4px;
  align-self: center;
}